.iconContainer {
  height: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;

  .icon {
    color: #f3f7ff;
    height: 30px;
    margin: 0 10px;
    cursor: pointer;
    transition: 0.2s;
    -webkit-transition: 0.2s;
    -moz-transition: 0.2s;

    &:hover {
      transform: scale(1.2);
      -webkit-box-shadow: 0 0 3pt 2pt #c4c9cb;
      -moz-box-shadow: 0 0 3pt 2pt #c4c9cb;
      box-shadow: 0 0 3pt 2pt #c4c9cb;
    }
  }

  .label {
    color: #f3f7ff;
  }
}
